.QuizList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, left top, right top, from(#fd8355), color-stop(37%, #f0576c), to(#f79cbd));
    background: -webkit-linear-gradient(left, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
    background: -o-linear-gradient(left, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
    background: linear-gradient(90deg, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
}

.QuizList h1 {
    color: #fff;
}

.QuizList ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.QuizList li {
    border: 1px solid #fff;
    padding: 10px;
    margin-bottom: 10px;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

.QuizList li a {
    text-decoration: none;
    color: #fff;

}

.QuizList li:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-box-shadow: 1px 1px 10px 1px #fff;
            box-shadow: 1px 1px 10px 1px #fff;

}


